*{
    margin: 0;
    padding: 0;
}

body{
    background-image: url(./IMG/bg-desktop.svg);
    background-color: hsl(257, 40%, 49%);
    margin-left: 70px;
    margin-right: 70px;

    header{
        width: 100%;
        height: 182px;

        img{
            width: 187px;
            height: 53px;
            margin-top: 39px;
            margin-bottom: 70px;
        }
    }

    main{
        width: 100%;
        height: 500px;

        display: flex;
        flex-direction: row;

        #layoutPhoto{
            margin-right: 20px;
            width:63%;

            img{
                width: 721px;
                height:498px;
            }
        }

        #text{
            width: 100%;
            height: 348px;

            .heading{
                width: 456px;
                height: 137px;

                h2{
                    color: white;
                    font-family: Poppins;
                    font-size:40px;
                    letter-spacing:1px;
                    margin-bottom:20px;
                }
            }

            .paragraph{
                width: 510px;

                p{
                    color: white;
                    font-family: Open Sans;
                    letter-spacing: 1px;
                }

            }
        }
    }

    footer{
        width: 100%;
        height: 88px;
        display: flex;
        flex-direction: row;
        justify-content: right;
        align-items: center;

        div{
            margin-left: 13px;
            

            a{
                text-decoration: none;
                color: white;
                
                
                span{
                    font-size: 14px;
                    border: 1px solid white;
                    border-radius: 50%;
                    padding: 8px;
                }
            }
        }
    }
}